.header{
  width: 100%;
  position: fixed;
  top: 30px;
  z-index: 1000;
  transition: .3s ease-in-out;
  &.scrolled{
    top: 0;
    .header{
      &-inner{
        border-radius: 3em;
        border-top-left-radius: 0rem;
        border-top-right-radius: 0rem;
        background-color: rgba(255, 255, 255, 0.9);
        border: 2px solid #000000ff;
        border-top: 0;
        padding-top: 0;
        padding-bottom: 0;
        padding-left: 40px;
        padding-right: 40px;
        height: 80px;
      }
    }
  }
  @keyframes headerSlide {
    0%{
      transform: scale(0);
      opacity: 0;
    }
  }
  @mixin headerAppearStart{
    pointer-events: none;
    transform: scale(0);
    opacity: 0;
  }
  @mixin headerAppearEnd{
    pointer-events: auto;
    transform: scale(1);
    opacity: 1;
  }


  @keyframes headerItemAppear1 {
    0%{
      @include headerAppearStart
    }
    5%{
      @include headerAppearStart
    }
    10%{
      @include headerAppearEnd
    }
  }

  @keyframes headerItemAppear2 {
    0%{
      @include headerAppearStart
    }
    6%{
      @include headerAppearStart
    }
    11%{
      @include headerAppearEnd
    }
  }

  @keyframes headerItemAppear3 {
    0%{
      @include headerAppearStart
    }
    7%{
      @include headerAppearStart
    }
    12%{
      @include headerAppearEnd
    }
  }

  @keyframes headerItemAppear4 {
    0%{
      @include headerAppearStart
    }
    8%{
      @include headerAppearStart
    }
    13%{
      @include headerAppearEnd
    }
  }

  @keyframes headerItemAppear5 {
    0%{
      @include headerAppearStart
    }
    9%{
      @include headerAppearStart
    }
    14%{
      @include headerAppearEnd
    }
  }

  $header_appear_time: 1s;
  $time_fraction: .25s;

  &-inner{
    height: 100px;
    background-color: rgba(255, 255, 255, 0.35);
    border: 2px solid #00000000;
    padding: 10px 60px;
    border-radius: 5em;
    transition: .3s;
    animation: headerSlide $header_appear_time ease-in-out;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__switch{
    $switch_height: 54px;
    &-wrapper{
      margin-left: 30px;
      //border: 2px solid rgb(0, 0, 0);
      //border-radius: $switch_height;
    }
    cursor: pointer;
    position: relative;
    display: inline-block;
    width: 104px;
    height: $switch_height;
    input {
      opacity: 0;
      width: 0;
      height: 0;
    }

    /* The slider */
    &-slider {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #ccc;
      transition: .2s;
      border-radius: $switch_height;
    }

    &-slider:before {
      border-radius: 50%;
      position: absolute;
      content: "";
      height: $switch_height - 8px;
      width: $switch_height - 8px;
      left: 4px;
      bottom: 0;
      top: 0;
      margin: auto;
      background-color: white;
      -webkit-transition: .4s;
      transition: .4s;
      z-index: 2;
    }

    input:checked + &-slider {
      background-color: #575757;
    }

    input:checked + &-slider::before {
      background-color: #2c2c2c;
    }

    input:checked + &-slider:before {
      left: calc(100% - $switch_height + 4px);
    }

    &-icon{
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
      height: 75%;
      path{
        transition: .2s;
        fill: white;
      }
      &.moon{
        left: 6px;
      }
      &.sun{
        right: 6px;
      }
    }
    input:checked ~ &-icon path{
      fill: black;
    }
  }

  &__nav{
    flex: 1;
    font-size: 24px;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    &-item{
      pointer-events: auto;
      padding: 10px 20px;
      cursor: pointer;
      transition: .3s;
      border-radius: 5em;
      border: 2px solid transparent;
      &:hover{
        transform: scale(1.05);
      }
      &.active{
        transform: scale(1.05);
        border: 2px solid black;
      }
      &.home{
        animation: headerItemAppear1 10s ease-in-out;
        &.active{
          background-color: rgba(239, 89, 89, 0.5);
        }
        &:hover{
          background-color: rgba(239, 89, 89, 0.8);
        }
      }
      &.skills{
        animation: headerItemAppear2 10s ease-in-out;
        &.active{
          background-color: rgba(107, 209, 246, 0.5);
        }
        &:hover{
          background-color: rgba(107, 209, 246, 0.8);
        }
      }
      &.projects{
        animation: headerItemAppear3 10s ease-in-out;
        &.active{
          background-color: rgba(158, 107, 246, 0.5);
        }
        &:hover{
          background-color: rgba(158, 107, 246, 0.8);
        }
      }
      &.about{
        animation: headerItemAppear4 10s ease-in-out;
        &.active{
          background-color: rgba(107, 246, 146, 0.5);
        }
        &:hover{
          background-color: rgba(107, 246, 146, 0.8);
        }
      }
      &.contact{
        animation: headerItemAppear5 10s ease-in-out;
        &.active{
          background-color: rgba(255, 129, 26, 0.5);
        }
        &:hover{
          background-color: rgba(255, 129, 26, 0.8);
        }
      }

    }

    &-dot{
      width: 10px;
      height: 10px;
      background-color: transparent;
      border: 2px solid black;
      border-radius: 50%;
    }
  }

  &__menu{
    background-color: transparent;
    border: none;
    border-radius: 12px;
    position: relative;
    height: 25px;
    width: 45px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0;
    cursor: pointer;
    transition: .3s;
    span{
      //position: absolute;
      width: 100%;
      height: 3px;
      background-color: black;
    }
  }

  @media screen and (max-width: 1200px) {
    top: 15px;
    &-inner{
      height: 80px;
      padding: 10px 15px;
    }
    &__nav{
      font-size: 18px;
      &-item{
        padding: 7px 15px;
      }
      &-dot{
        width: 7px;
        height: 7px;
      }
    }
  }

  @media screen and (max-width: 970px) {
    &__nav{
      &-dot{
        display: none;
      }
    }
    &__switch{
      &-wrapper{
        margin-left: 20px;
      }

      width: 84px;
      height: 42px;

      &-slider{
        &::before{
          height: 34px;
          width: 34px;
        }
      }
      input:checked + &-slider:before {
        left: calc(100% - 42px + 4px);
      }
    }
  }
  @media screen and (max-width: 800px) {
    &__nav{
      font-size: 16px;
    }
  }
  @media screen and (max-width: 767px) {
    &-inner{
      padding: 15px 30px;
    }

    &.scrolled{
      .header{
        &-inner{
          border-radius: 1.5em;
          border-top-left-radius: 0;
          border-top-right-radius: 0;
          height: 60px;
          padding: 10px 20px;
        }

      }
    }
  }
  @media screen and (max-width: 530px) {
    &-inner{
      height: 60px;
      padding: 15px 10px;
    }
    &__menu{
      margin-left: 20px;
    }
  }
  &.scrolled{
    .header{
      &-inner{
        padding: 10px;
      }
      &__menu{
        margin-left: 8px;
      }
    }
  }
}