.background{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  margin: auto;
  width: 100%;
  height: 100%;
}
.rainbow{
  -webkit-appearance: none;
  position: relative;
  background:  linear-gradient(to right,
          #d7bdde, #bebbe3, #b0cfe0,
          #b5d5b9, #d5d5b7,#DDBDC0);

  //background:  linear-gradient(to right,
          //#0a0a0a, #181818, #070b38, #051a00);
          //#012f09, #363600, #6b000b);
  //
  //  background:  linear-gradient(to right,
  //          #a803dc, #5c52be, #247baf,
  //          #36943e, #afaf29, #ea0b1c);

  background-size: 800%;
  transition: .3s;
  animation: rainbow 20s linear infinite;
  width: 100%;
  height: 100%;
  z-index: 100;
  overflow: hidden;
}

$initial_scale: scale(1.5);

.img-bg{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  opacity: 1;
  animation: zoom 50s ease-in-out infinite;
  transform: $initial_scale;
  filter: blur(0px);
}

@keyframes rainbow {
  0% {
    background-position: 0 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0 50%;
  }
}

@keyframes zoom {
  0% {
    transform: $initial_scale;
    filter: blur(4px);
  }

  50% {
    transform: scale(1);
    filter: blur(0px);
  }

  100% {
    transform: $initial_scale;
    filter: blur(4px);
  }
}