.proj{
  &ects{
    overflow-y: hidden;
  }
  padding-bottom: 30px;
  &-grid{
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 15px;
  }
  &__item{
    color: black;
    width: 100%;
    border-radius: 1.5em;
    //background-color: #c9bdde;
    //padding: 30px 40px;
    font-size: 16px;
    transition: .3s transform;
    a{
      color: black;
    }

    &:hover{
      transform: scale(1.02);
    }

    &-inner{
      height: 100%;
      padding: 30px 30px;
      border-radius: 1.5em;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      background-size: 600%;
    }
    &.white{
      color: white;
      .proj__item{
        &-title{
          color: white;
          &::before{
            background-color: white;
          }
        }
        &__links{
          a{
            color: white;
          }
          svg{
            path{
              fill: white;
            }
          }
          &:hover{
            a{
              color: black;
            }
            svg{
              path{
                fill: black;
              }
            }
          }
        }
        &-tech{
          &--title{
            &::before,
            &::after{
              background-color: white;
            }
          }
        }
      }
    }
    &-title{
      //font-family: "Kanet", sans-serif;
      //letter-spacing: 6px;
      font-size: 24px;
      padding-left: 50px;
      position: relative;
      color: black;
      //-webkit-text-stroke-color: black;
      //-webkit-text-stroke-width: 3px;
      &::before{
        content: "";
        position: absolute;
        width: 40px;
        height: 3px;
        background-color: black;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto;
      }
    }
    &-year{
      margin-top: 10px;
    }
    &-ml{
      margin-left: 10px;
    }
    &-type{
      margin-top: 10px;
    }

    &-tech{
      margin-top: 10px;
      display: flex;
      flex-direction: column;
      align-items: center;
      overflow: hidden;
      &--title{
        display: inline-block;
        text-align: center;
        margin: 0 auto;
        margin-bottom: 15px;
        position: relative;
        &::before{
          content: "";
          position: absolute;
          width: 100%;
          height: 3px;
          background-color: black;
          left: calc(-100% - 10px);
          top: 0;
          bottom: 0;
          margin: auto;
        }
        &::after{
          content: "";
          position: absolute;
          width: 100%;
          height: 3px;
          background-color: black;
          right: calc(-100% - 10px);
          top: 0;
          bottom: 0;
          margin: auto;
        }

      }
      $tech_img_height: 36px;
      $tech_margin: 10px;
      &--img{
        &---wrapper{
          height: calc($tech_img_height + $tech_margin);
          padding-right: $tech_margin;
          padding-bottom: $tech_margin;
        }
        min-height: $tech_img_height;
        max-height: $tech_img_height;
      }
      &__wrap{
        &-outer{
          border-radius: 10px;
          padding: 10px;
          backdrop-filter: blur(10px);
          background-color: rgba(255, 255, 255, 0.7);
        }
        max-width: 360px;
        margin-right: -$tech_margin;
        margin-bottom: -$tech_margin;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: center;
      }
    }

    &__links{
      margin-top: 15px;
      display: flex;
      justify-content: space-between;
      &:hover{
        a{
          color: white;
        }
        svg{
          path{
            fill: white;
          }
        }
      }
      a{
        display: flex;
        align-items: center;
        transition: .3s;
        svg{
          height: 26px;
          width: auto;
          margin: 0 5px;
          path{
            transition: .3s;
          }
        }
      }
    }
    .loader{
      //transform: scale(1.25);
      transform: scale(.9);
    }

    $img_height: 195px;

    &__img{
      &-wrapper{
        display: grid;
        place-items: center;
        background-color: white;
        margin-top: 20px;
        overflow: hidden;
        border-radius: 1.5em;
        width: 100%;
        max-height: $img_height;
        height: $img_height;
        position: relative;
        &:hover .proj__item__img{
          transition: 5s linear;
          transform: translateY(calc(-100% + $img_height));
        }
        border: 2px solid #ffffff;
      }
      opacity: 0;
      //display: none;
      transition: 1s linear;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      min-height: $img_height;
      max-width: 100%;
    }
  }

  @media screen and (max-width: 1200px){
    &-grid{
      grid-template-columns:  1fr 1fr;
    }
  }
  @media screen and (max-width: 768px){
    &-grid{
      grid-template-columns:  1fr;
    }
  }
  @media screen and (max-width: 470px){
    &__item{
      &-inner{
        padding: 20px;
      }
      &-title{
        font-size: 20px;
        padding-left: 32px;
        &::before{
          width: 25px;
        }
      }
      &-year, &-type, {
        margin-top: 5px;
      }
      &-tech{
        &--title{
          font-size: 14px;
          margin-bottom: 10px;
        }
      }
    }
  }
}