.contact{
  &-inner{
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
  &__block{
    &.info{
      width: 100%;
      font-size: 26px;
    }
    &.form{
      width: 100%;
      .tooltip{
        left: 0;
        margin: auto;
        max-width: 300px;
        //height: 90px;

        &.active{
          bottom: calc(100% + 25px);
        }
        &-inner{
          clip-path: polygon(100% 0, 100% calc(100% - 15px), 45% calc(100% - 15px), 50% 100%, 55% calc(100% - 15px), 0 calc(100% - 15px), 0 0);
        }
      }
    }


    .highlight{
      display: inline-block;
      position: relative;
      strong{
        text-decoration: underline;
        text-underline-offset: 7px;
        font-size: 1.2em;
        cursor: pointer;
      }
    }
  }

  .tooltip{
    font-size: 18px;
    z-index: 50;
    position: absolute;
    bottom: calc(100% + 45px);
    right: 0;
    background: transparent;
    filter: drop-shadow(-6px 6px 4px rgba(0, 0, 0, 0.5));
    opacity: 0;
    pointer-events: none;
    transition: .2s;
    &.active{
      bottom: calc(100% + 15px);
      opacity: 1;
      pointer-events: auto;
    }
    &-inner{
      margin-bottom: -15px;
      padding: 20px;
      width: 100%;
      height: calc(100% + 15px);
      clip-path: polygon(100% 0, 100% calc(100% - 15px), 70% calc(100% - 15px), 75% 100%, 80% calc(100% - 15px), 0 calc(100% - 15px), 0 0);
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
      background-color: rgb(231, 205, 40);
      & > *{
        margin-bottom: 15px;
      }
      span{
        width: 250px;
        display: inline-block;
      }
    }
    span{
      word-break: keep-all;
      //display: block;
    }
    svg{
      padding: 5px;
      border-radius: 50%;
      background-color: #00a400;
      max-height: 36px;
      path{
        fill: white;
      }
    }
  }

  .phone-number{
    display: inline-block;
    text-decoration: underline;
    text-underline-offset: 7px;
    font-size: 1.2em;
  }

  &__socials{
    display: flex;
    gap: 30px;
    &__item{
      //width: 70px;
      //height: 70px;
      //background: rgba(255, 255, 255, 0);
      //border: 2px solid black;
      //border-radius: 50%;
      display: grid;
      place-items: center;
      &:hover {
        &.linkedin{
          svg{
            color: #0077b5;
          }
        }
        &.github{
          svg{
            color: #8957E5;
          }
        }
        &.telegram{
          svg{
            color: #2AABEE;
          }
        }
        &.whatsapp{
          svg{
            color: #25D366;
          }
        }
      }
      svg{
        max-height: 70px;
        height: 100%;
        width: 100%;
        color: rgba(0, 0, 0, 0.8);
        path{
          transition: .3s;
        }
      }
    }
  }

  &__title{
    margin-bottom: 30px;
    font-size: 52px;
    position: relative;
    padding-left: 90px;
    &::before{
      //display: inline-block;
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      background-color: black;
      color: white;
      width: 75px;
      height: 3px;
    }
  }

  &__form{
    &-inner{
      position: relative;
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 30px;
    }

    &-group{
      display: flex;
      flex-direction: column;
      position: relative;
      &.active{
        .contact__label{
          font-size: 16px;
        }
      }

      &.textarea{
        grid-column: 1 / 3;
      }
      &.btn{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        grid-column: 1 / 3;
      }
    }

    &.disabled{

      input, textarea, label{
        opacity: .1;
      }
      //.contact__form{
      //
      //}
    }

  }

  &__loader{
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    position: absolute;
    max-height: 25%;
    height: 100%;
  }

  input.contact__send{
    border: 3px solid  rgba(0,0,0,.5);
    border-radius: 1em;
    font-size: 28px;
    font-weight: 700;
    background: transparent;
    padding: 20px 40px;
    cursor: pointer;
    letter-spacing: 2px;
    transition: .6s;
    box-shadow: inset 0 0 0px black;
    //display: flex;
    //justify-content: space-between;
    gap: 1px;
    &:hover{
      border-radius: 0em;
      border-color: transparent;
      //gap: 50px;
      padding: 20px 80px;
      //font-size: 32px;
      //letter-spacing: 50px;
      //color: white;
      $send_color: rgba(0,0,0,.5);
      box-shadow:
              inset .2em 0 0px 10px $send_color,
              inset -.2em 0 0px 10px $send_color,
              inset 0 .2em 0px 10px $send_color,
              inset 0 -.2em 0px 10px $send_color;
    }

    &:disabled{
      cursor: not-allowed;
    }
  }

  &__label{
    transition: .2s;
    font-size: 46px;
    padding-left: 20px;
    position: absolute;
    top: 20px;
  }
  &__textarea, &__input{
    border: 2px solid  rgba(0,0,0,.5);
    border-radius: 1em;
    width: 100%;
    padding: 20px;
    //padding-top: 46px;
    padding-top: 46px;
    background: transparent;
    font-size: 24px;
    &:hover,
    &:focus, {
      & + label{
        font-size: 16px;
      }
    }
    &:focus{
      &:invalid{
        border-color: rgba(194, 1, 1, 0.5);
      }
    }
    &:disabled{
      cursor: not-allowed;
    }

  }
  &__textarea{
    padding-top: 46px;
    min-height: 200px;
    height: auto;
    overflow: hidden;
  }
  &__input{
    //height: 56px;
  }

  @media screen and (max-width: 1000px){
    &__title{
      font-size: 36px;
      padding-left: 50px;
      &::before{
        width: 40px;
      }
    }
  }
  @media screen and (max-width: 768px){
    &__label{
      font-size: 36px;
      top: 13px;
      padding-left: 15px;
    }
    &__textarea, &__input{
      font-size: 16px;
      padding: 15px;
      padding-top: 32px;
      &:hover,
      &:focus, {
        & + label{
          font-size: 12px;
        }
      }
    }
    &__form-group.active .contact__label{
      font-size: 12px;
    }

    &__socials{
      justify-content: center;
    }
  }

  @media screen and (max-width: 680px) {
    &__form{
      &-inner{
        grid-template-columns: 1fr;
        grid-gap: 15px;
      }
    }
    &__form-group{
      &.textarea,
      &.btn{
        grid-column: initial;
      }
    }
  }
  @media screen and (max-width: 600px) {
      &__block{
        &.info{
          font-size: 20px;
        }
      }
  }
  @media screen and (max-width: 480px) {
    &__title {
      font-size: 24px;
    }
    &__socials{
      gap: 0;
      justify-content: space-evenly;
      &__item{
        svg{
          max-height: 50px;
        }
      }
    }
    input.contact__send{
      border-width: 2px;
      border-radius: 16px;
      width: 100%;
    }
    }
  @media screen and (max-width: 390px) {
    &__block{
      &.info{
        font-size: 16px;
      }
    }
    .tooltip{
      font-size: 15px;
      &-inner{
        span{
          width: 210px;
        }
      }
    }
  }
  }