.about{
  $trans: 1s ease-in-out;
  &__block{
    margin-bottom: 50px;
    &:last-child{
      margin-bottom: 0;
    }
  }
  &__title{
    margin-bottom: 15px;
    font-size: 52px;
    position: relative;
    padding-left: 90px;
    &::before{
      //display: inline-block;
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto;
      background-color: black;
      color: white;
      width: 75px;
      height: 3px;
    }
  }
  &__top{
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  &__name{
    font-size: 100px;
    text-align: center;
    .name{
      text-transform: capitalize;
    }
    .surname{
      text-transform: uppercase;
    }
  }
  &__img{
    max-height: 300px;
    border-radius: 50%;
    margin-right: 60px;
    box-shadow: 0px 0px 20px 0px black;
  }

  &__text{
    padding-left: 50px;
    font-size: 28px;
    line-height: 170%;
  }

  &__timeline{
    margin-top: 30px;
    background-color: rgba(181, 213, 185, 0.7);
    border-radius: 1.5em;
    -webkit-tap-highlight-color: transparent;
    & *{
      -webkit-tap-highlight-color: transparent;
    }
    &-inner{
      padding: 20px;
      padding-bottom: 40px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      //transition: $trans;
      //overflow: hidden;
      position: relative;
    }
    .only_min{
      display: none;
    }
    &-btn{
      appearance: none;
      border: 0;
      position: absolute;
      top: 10px;
      right: 0;
      left: 0;
      margin: auto;
      width: fit-content;
      z-index: 10;
      cursor: pointer;
      padding: 10px;
      //border: 1px solid white;
      //border-radius: 50%;
      background-color: transparent;
      transition: .3s;

      &:hover{
        svg{
          transform: rotate(-90deg) scale(1.1);
        }
      }
      svg{
        transition: .3s;
        width: 36px;
        height: 36px;
        transform: rotate(-90deg);
      }
    }
    &.minimized {
      cursor: pointer;
      transition: .3s ease-in-out transform;
      &:hover{
        .about__timeline__year:last-child .about__timeline__months__item:last-child::before{
          transform: rotate(90deg) scale(1.15);
        }
      }
      .only_min{
        display: inline;
      }
      .about__timeline{
        &-btn{
          opacity: 0;
          pointer-events: none;
          top: -60px;
        }
        &-inner{
          padding-top: 20px;
        }
        &__year{
          grid-template-columns: 1fr 0px;
          grid-column-gap: 0;
          transition: .5s grid-template-columns, .5s grid-column-gap;
          &-title{
            height: 0;
            opacity: 0;
            transition: none;
          }
          &:last-child{
            .about__timeline__months__item:last-child{
              display: block;
              opacity: 1;
              height: 120px;
              border-bottom: 2px solid rgba(0, 0, 0, 0.5);
              padding-bottom: 6px;
              overflow: visible;
              & > span{
                bottom: 6px;
              }
              &::before{
                transition: .3s;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                transform: rotate(90deg);
                content: "";
                width: 50px;
                height: 50px;
                background-image: url("../../img/svg/double_arrow.svg");
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
                background-color: transparent;
              }
            }
          }
        }
        &__months{
          &__item{
            padding: 0;
            border-bottom: 0px;
            height: 0;
            overflow: hidden;
            &.work_start{
              overflow: visible;
              height: 60px;
              padding-bottom: 6px;
              border-bottom: 2px solid rgba(0, 0, 0, 0.5);
            }
          }
        }
        &__work{
          &-line{
            height: calc(60px*2 - 20px) !important;

          }
        }
      }
    }
    &__year{
      display: grid;
      grid-template-columns: 1fr 30px;
      grid-column-gap: 15px;
      transition: $trans;
      &-title{
        font-size: 32px;
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 7.5px;
        writing-mode: vertical-rl;
        text-orientation: mixed;
        padding-top: 0px;
        letter-spacing: 3px;
        transition: $trans;
        &::before,
        &::after{
          content: "";
          background-color: black;
          width: 2px;
        }
        &::before{
          max-height: 20px;
          flex: 1;
        }
        &::after{
          flex: 1;
        }
      }

      &:first-child &-title{
        padding-top: 30px;
      }
    }
    &__months{
      //position: absolute;
      //right: 0;
      //top: 0;
      width: 100%;
      &__item{
        position: relative;
        height: 60px;
        padding-bottom: 6px;
        font-size: 20px;
        text-align: left;
        display: flex;
        align-items: flex-end;
        border-bottom: 2px solid rgba(0, 0, 0, 0.5);
        transition: .2s ease-in-out height;
        & > span{
          position: absolute;
          right: 0;
          font-size: 12px;
          span{
            transition: 0s color;
          }
        }

        &-position{
          padding-left: 120px;
        }
      }
    }
    &__work{
      position: relative;
      padding-left: 60px;
      font-size: 30px;
      &::before{
        content: "";
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: black;
        position: absolute;
        left: 20px;
        top: 0;
        bottom: 0;
        margin: auto;
      }
      &-line{
        position: absolute;
        left: 28px;
        top: 20px;
        background-color: black;
        width: 4px;
        &--inner{
          display: block;
          width: 100%;
          height: 100%;
          position: relative;

          &::before{
            content: "";
            width: 20px;
            height: 20px;
            border-radius: 50%;
            background-color: #000000;
            position: absolute;
            left: -8px;
            bottom: -20px;
            margin: auto;
          }
          &.current{
            &::before{
              height: 25px;
              border-radius: 0;
              clip-path: polygon(50% 20%, 100% 0, 50% 100%, 0 0);
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 1300px) {
    &__top{
      gap: 30px;
      justify-content: space-between;
    }
    &__name{
      font-size: 80px;
    }
    &__img{
      margin-right: 0;
    }
  }
  @media screen and (max-width: 1090px) {
    &__top{
      flex-direction: column-reverse;
    }
    &__timeline{
      &__work{
        font-size: 22px;
      }
      &__months{
        &__item{
          font-size: 14px;
        }
      }
    }
  }
  @media screen and (max-width: 860px) {
    &__timeline{
      &__months{
        &__item{
          &-position{
            padding-left: 65px;
          }
        }
      }
      &__work{
        &-currently{
          display: none !important;
        }
      }

    }
  }
  @media screen and (max-width: 800px) {
    &__name{
      font-size: 64px;
    }
  }
  @media screen and (max-width: 768px) {
    &__title{
      font-size: 36px;
      padding-left: 52px;
      &::before{
        width: 40px;
      }
    }
    &__text{
      font-size: 22px;
    }

  }
  @media screen and (max-width: 768px) {
    &__timeline {
      &__work{
        padding-left: 30px;
        font-size: 16px;
        &::before{
          left: 10px;
          width: 12px;
          height: 12px;
        }
        &-line{
          top: 18px;
          left: 15px;
          width: 2px;
          &--inner{
            &.current{
              &::before{
                width: 14px;
                height: 18px;
                left: -6px;
                bottom: -15px;
              }
            }
            &::before{
              width: 12px;
              height: 12px;
              left: -5px;
              bottom: -12px;
            }
          }
        }
      }
      &__months{
        &__item{
          height: 40px;
          font-size: 12px;
          &-position{
            padding-left: 45px;
          }
        }
      }
      &__year{
        &-title{
          font-size: 18px;
          letter-spacing: 0;
        }
        grid-template-columns: 1fr 14px;
        grid-column-gap: 8px;
      }
      &-inner{
        padding-top: 30px;
      }
      &-btn{
        top: 14px;
        padding: 0;
        svg{
          width: 24px;
          height: 24px;
        }
      }
    }
    .about__timeline.minimized .about__timeline__year:last-child .about__timeline__months__item:last-child::before{
      width: 30px;
      height: 30px;
    }
    .about__timeline.minimized .about__timeline__months__item.work_start{
      height: 40px;
    }
    .about__timeline.minimized .about__timeline__year:last-child .about__timeline__months__item:last-child{
      height: 80px;
    }
    .about__timeline.minimized .about__timeline__work-line{
      height: 60px !important;
    }
    }

  @media screen and (max-width: 610px) {
    .about__timeline__months__item > span{
      font-size: 9px;
    }
    .about__timeline__months__item-position{
      padding-left: 30px;
      //text-overflow: ellipsis;
      //white-space: nowrap;
      //overflow: hidden;
      width: 100%;
      max-width: calc(100% - 70px);
    }
    .about__timeline__work{
      width: 100%;
      max-width: calc(100% - 70px);
      &-title{
        display: block;
        line-height: 80%;
      }
    }
    .about__timeline__work-line{
      top: 10px;
    }
    &__top{
      margin-top: 0;
    }
    &__img{
      margin-top: 10px;
      border-radius: 1em;
    }
  }
  @media screen and (max-width: 590px) {
    &__name{
      font-size: 36px;
    }
    &__title{
      font-size: 24px;
      margin-bottom: 4px;
    }
    &__text{
      font-size: 16px;
    }
  }
  @media screen and (max-width: 430px) {
    &__text{
      padding-left: 0;
    }
    .about__timeline__months__item-position{
      display: none;
    }
    &__img{
      max-height: initial;
      width: 100%;
    }
  }
  @media screen and (max-width: 380px) {
    &__name{
      font-size: 32px;
    }
  }
  @media screen and (max-width: 360px) {
    .about__timeline__work{
      font-size: 12px;
    }
  }
    @media screen and (max-width: 335px) {
    .about__timeline__work{
      font-size: 11px;
    }
  }

  }