// COMMON
html{
  scroll-behavior: smooth;
}

body{
  overflow: overlay;
  overflow-x: hidden;
  &::-webkit-scrollbar{
    width: 1em;
    background: rgba(0,0,0,0);
    &-thumb{
      border-radius: 2em;
      background-color: rgba(117, 117, 117, 0.5);
      &:hover{
        background-color: rgba(117, 117, 117, 0.7);
      }
    }
  }
}

* {
  &::selection{
    background: rgba(0, 0, 0, 0.66);
    color: white;
  }
}

textarea, input, button{
  font-family: "Unbounded", sans-serif;
  color: black;
  appearance: none;
  resize: none;
  outline: none;
}

.app{
  overflow-x: hidden;
  //transform: scale(.5);
  min-height: 100vh;
  &.blocked{
    max-height: 100vh;
    height: 100vh;
    overflow-y: hidden;
  }
}

.container{
  margin: 0 auto;
  max-width: 1400px;
  width: 100%;
  height: 100%;
  padding: 0 15px;
}

a{
  color: black;
  text-decoration: none;
}

.card{
  padding: 30px;
  height: 100%;
  border-radius: 1.5em;
  transition: .3s;
}

.card-hover{
  background-color: rgba(255, 255, 255, 0.35);
  border: 2px solid rgba(255, 255, 255, 0);
  backdrop-filter: blur(5px);
  &:hover{
    background-color: rgba(255, 255, 255, 0.7);
  }
}

.main{
  margin-top: 160px;
  position: relative;
  width: 100%;
  max-width: 200vw;
  transform: translateX(0);
  //overflow: hidden;
}

.section{
  padding-bottom: 30px;
  width: 100%;
  //max-width: 100vw;
  //min-width: 100vw;
  display: inline-block;
}

@media screen and (max-width: 1200px) {
  .main{
    margin-top: 115px;
  }
}
@media screen and (max-width: 768px) {
  .card{
    padding: 15px;
  }
}
@media screen and (max-width: 530px) {
  .main{
    margin-top: 90px;
  }
}
