.app{
  &.dark{
    color: white;
    textarea, input, button {
      color: white;
    }
    * {
      &::selection{
        background: rgba(255, 255, 255, 0.66);
        color: black;
      }
    }
    a{
      color: white;
    }
    .card-hover{
      border: 2px solid rgba(255, 255, 255, 0.5);
      background-color: rgba(0, 0, 0, 0.35);
      &:hover{
        background-color: rgba(0, 0, 0, 0.7);
      }
    }
    .rainbow{
      background:  linear-gradient(to right,
              #25013a, #080036, #002842,
              #012f09, #363600, #6b000b);
      background-size: 800%;
    }
    .header{
      &.scrolled{
        .header{
          &-inner{
            background-color: rgba(0, 0, 0, 0.9);
            border: 2px solid rgba(255, 255, 255, 0.5);
            border-top: 0;
          }
        }
      }
      &__nav{
        &-item{
          &.active{
            border-color: rgba(255, 255, 255, 0.5);
          }
        }
        &-dot{
          border-color: white;
        }
      }
      &__menu{
        span{
          background-color: rgba(255, 255, 255, 0.7);
        }
      }
    }

    .menu{
      background-color: rgba(0,0,0,.3);
      &__nav{
        &-line{
          background-color: rgba(255, 255, 255, 0.7);
        }
      }
    }

    .intro{
      &__string{
        &.name,
        &.frontend,
        &.location{
          span{
            &:first-child{
              &::before{
                background-color: white;
              }
            }
          }
        }
      }
      &__socials{
        &-item{
          svg{
            color: rgba(255, 255, 255, 0.8);
          }
        }
      }
    }

    .proj{
      &__item{

        &__links{
          &:hover{
            a{
              color: white;
            }
          }
          a{
            color: black;

          }
        }
      }
    }
    .skills{
      &__item{
        background-color: rgba(255, 255, 255, 0.1);
        &:hover{
          box-shadow: 0 0 50px 6px rgba(255, 255, 255, 0.5),
          inset 0 0 50px 6px rgba(255, 255, 255, 0.5);
        }
        &__rating{
          &-star{
            path{
              stroke: rgba(255, 255, 255, 0.9);
            }
          }
        }
      }
    }

    .about{
      &__title{
        &::before{
          background-color: white;
        }
      }
      &__img{
        box-shadow: 0px 0px 20px 0px white;
      }
      &__timeline{
        &-btn{
          svg{
            path{
              fill: white;
            }
          }
        }
        &__year{
          &-title{
            &::before,
            &::after{
              background-color: white;
            }
          }
          &:last-child{
            .about__timeline__months__item:last-child{
              &::before{
                background-image: url("img/svg/double_arrow_white.svg") !important;
              }
            }
          }
        }
        &__months{
          &__item{
            border-bottom: 2px solid rgba(255, 255, 255, 0.5);
            //&::after{
            //  background-color: rgba(255, 255, 255, 0.5);
            //}
          }
        }
        &__work{
          &::before{
            background-color: white;
          }
          &-line{
            background-color: white;
            &--inner{
              &::before{
                background-color: white;
              }
            }
          }
        }

        &.minimized{
          .about__timeline__months__item.work_start{
            border-color: rgba(255, 255, 255, 0.5);
          }
          .about__timeline__year:last-child .about__timeline__months__item:last-child{
            border-color: rgba(255, 255, 255, 0.5);
          }
        }
      }
    }

    .contact{
      &__textarea,&__input,&__send{
        border-color: rgba(255, 255, 255, 0.5);
      }
      input.contact__send{
        &:hover{

          $send_color: rgba(255, 255, 255, 0.5);
          box-shadow:
                inset .2em 0 0px 10px $send_color,
                inset -.2em 0 0px 10px $send_color,
                inset 0 .2em 0px 10px $send_color,
                inset 0 -.2em 0px 10px $send_color;
        }
      }

      &__title{
        &::before{
          background-color: white;
        }
      }
      .tooltip{
        filter: drop-shadow(-6px 6px 2px rgba(255, 255, 255, 0.6));
        &-inner{
          background-color: rgb(166, 151, 29);

        }
      }

      &__socials{
        &__item{
          svg{
            color: rgba(255, 255, 255, 0.8);
          }
        }
      }

    }

  }
}
