.menu{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(30px);
  z-index: 1000;
  transition: .7s;
  &.hidden{
    transform: translateX(-100%);
  }
  &-inner{
    position: relative;
    width: 100%;
    height: 100%;
  }
  &-close{
    position: absolute;
    top: 15px;
    right: 15px;
    background-color: transparent;
    border: none;
    font-size: 36px;
  }
  &__nav{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 100px 0;
    &-line{
      margin: 0 auto;
      width: 50%;
      height: 3px;
      background-color: black;
    }
    &-item{
      display: grid;
      place-items: center;
      width: 100%;
      flex: 1;
      transition: .3s;

      &.home{
        animation: headerItemAppear1 10s ease-in-out;
        &.active{
          background-color: rgba(239, 89, 89, 0.5);
        }
        &:hover{
          background-color: rgba(239, 89, 89, 0.8);
        }
      }
      &.skills{
        animation: headerItemAppear2 10s ease-in-out;
        &.active{
          background-color: rgba(107, 209, 246, 0.5);
        }
        &:hover{
          background-color: rgba(107, 209, 246, 0.8);
        }
      }
      &.projects{
        animation: headerItemAppear3 10s ease-in-out;
        &.active{
          background-color: rgba(158, 107, 246, 0.5);
        }
        &:hover{
          background-color: rgba(158, 107, 246, 0.8);
        }
      }
      &.about{
        animation: headerItemAppear4 10s ease-in-out;
        &.active{
          background-color: rgba(107, 246, 146, 0.5);
        }
        &:hover{
          background-color: rgba(107, 246, 146, 0.8);
        }
      }
      &.contact{
        animation: headerItemAppear5 10s ease-in-out;
        &.active{
          background-color: rgba(255, 129, 26, 0.5);
        }
        &:hover{
          background-color: rgba(255, 129, 26, 0.8);
        }
      }


    }
  }
}