.intro{
  &-inner{
    //min-height: calc(100vh - 160px - 40px);
    overflow: hidden;
  }
  &__welcome{
    position: relative;
    height: calc(100vh - 160px - 60px - 40px);
    //height: 700px;
    &-inner{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  $startFontSize: 100px;
  $endFontSize: 52px;

  &__string{
    text-align: center;
    font-size: $startFontSize;
    font-weight: 700;
    transition: .2s;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    span{
      user-select: none;
      position: relative;
      top: -100px;
      transition: .3s;
      opacity: 0;
      transform: translateY(-100px);
      &.anim{
        top: 0;
        opacity: 1;
        transform: translateY(0px);
        animation: letter .5s linear;
      }
    }

    &.oneLine{
      display: flex;
      span{
        min-width: .25em;
      }
    }

    &.hide{
      opacity: 0;
    }

    @mixin endingState ($top){
      top: $top;
      left: 0;
      transform: translate(0,0);
      font-size: $endFontSize;
    }

    $gap_between: 68px;
    $name_top: 0px;
    $frontend_top: $name_top + $gap_between;
    $location_top: $frontend_top + $gap_between;


    &.name,
    &.frontend,
    &.location{
      padding-left: 100px;
      span{
        user-select: auto;
        &:first-child{
          &::before{
            width: 80px;
            height: 3px;
            top: 0;
            bottom: 0;
            margin: auto;
            background-color: black;
            content: "";
            display: block;
            position: absolute;
            right: calc(100% + 20px);
          }
        }
      }
    }

    @keyframes nameTran {
      0%{
        font-size: $startFontSize;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      50%{
        font-size: $startFontSize;
        top: $name_top;
        left: 0;
        transform: translate(0,0);
      }
      100%{
        font-size: $endFontSize;
      }
    }

    &.name{
      animation: nameTran .5s ease-in-out;
      @include endingState($name_top)
    }

    @keyframes frontendTran {
      0%{
        font-size: $startFontSize;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      50%{
        font-size: $startFontSize;
        top: $frontend_top;
        left: 0;
        transform: translate(0,0);
      }
      100%{
        font-size: $endFontSize;
      }
    }

    &.frontend{
      animation: frontendTran .5s ease-in-out;
      @include endingState($frontend_top)

    }

    @keyframes locationTran {
      0%{
        font-size: $startFontSize;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      50%{
        font-size: $startFontSize;
        top: $location_top;
        left: 0;
        transform: translate(0,0);
      }
      100%{
        font-size: $endFontSize;
      }
    }

    &.location{
      animation: locationTran .5s ease-in-out;
      @include endingState($location_top)
    }
  }

  &__socials{
    display: flex;
    gap: 15px;
    //overflow: hidden;
    //&.hidden{
    //  display: none;
    //}
    right: 0;
    bottom: 0;
    position: absolute;
    &-item{
      display: grid;
      place-items: center;
      transform: translateY(100%);
      opacity: 0;
      pointer-events: none;

      @keyframes socials {
        0%{
          transform: translateY(100%) scaleX(.9);
        }
        50%{
          opacity: 1;
          transform: translateY(-25%) scaleX(.9);
        }
        90%{
          transform: translateY(5%) scaleX(1.1);
        }
        100%{
          transform: translateY(0%) scaleX(1);
        }
      }

      &.anim{
        transform: translateY(0%) scaleX(1);
        animation: .5s socials ease-in-out;
        opacity: 1;
        pointer-events: auto;
      }

      &:hover {
        &.linkedin{
          svg{
            color: #0077b5;
          }
        }
        &.github{
          svg{
            color: #8957E5;
          }
        }
        &.telegram{
          svg{
            color: #2AABEE;
          }
        }
        &.whatsapp{
          svg{
            color: #25D366;
          }
        }
      }
      svg{
        max-height: 70px;
        height: 100%;
        width: 100%;
        color: rgba(0, 0, 0, 0.8);
        path{
          transition: .3s;
        }
      }
    }
  }

  &__repeat{
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    width: 72px;
    height: 72px;
    //padding: 20px;
    font-size: 52px;
    border-radius: 50%;
    background-color: rgba(239, 89, 89, 0.5);
    transition: .3s;
    &:hover{
      background-color: rgba(239, 89, 89, 0.8);
      svg{
        //transform: rotate(180deg);
      }
    }
    svg{
      transform-origin: center;
      transition: .3s;
    }
  }
  
