.skills{
  &-grid{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    //flex-direction: column;
    //grid-template-columns: repeat(5, 1fr);
    gap: 15px;
    //max-height: 900px;
  }
  &__item{
    position: relative;
    //width: 25%;
    overflow: hidden;
    flex: 20%;
    background-color: rgba(0, 0, 0, 0.1);
    padding: 30px;
    border-radius: 1.5em;
    //backdrop-filter: blur(10px);
    transition: .3s;
    &:hover{
      box-shadow: 0 0 50px 6px rgba(0, 0, 0, 0.5),
      inset 0 0 50px 6px rgba(0, 0, 0, 0.5);
      background-color: rgba(208, 208, 208, 0);
      transform: scale(1.03);
    }
    &:hover &__img-bg{
      height: 120%;
      filter: blur(150px);
      opacity: 1;
    }

    &__img{
      &-wrapper{
        //min-height: 100%;
        position: relative;
        text-align: center;
        margin-bottom: 15px;
      }
      transition: .5s;
      max-height: 100px;
      max-width: calc(100%);
      //display: none;
      &-bg{
        z-index: -1;
        filter: blur(50px);
        opacity: .5;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        height: 75%;
        //width: 100%;
        transition: .5s;
      }
    }
    &-title{
      text-align: center;
      font-weight: 700;
      font-size: 24px;
      margin-bottom: 15px;
    }
    &__rating{
      font-size: 30px;
      display: flex;
      gap: 10px;
      justify-content: center;
      &-star{
        height: 40px;

        path{
          stroke: rgba(0,0,0,1);
          stroke-width: .3px;
          fill: #fdeb22;
        }
      }
    }
  }

  @media screen and (max-width: 1200px) {
    &__item{
      flex: 25%;
    }
  }
  @media screen and (max-width: 900px) {
    &__item{
      flex: calc(100%/3);
    }
  }
  @media screen and (max-width: 600px) {
    &__item{
      flex: 100%;
      padding: 20px;
      &__img{
        max-height: 80px;
      }
      &-title{
        font-size: 20px;
      }
      &__rating{
        &-star{
          height: 30px;
        }
      }
    }
  }
  @media screen and (max-width: 1000px) {

  }
  @media screen and (max-width: 1000px) {

  }
  @media screen and (max-width: 1000px) {

  }

}